$main-grey: #CEE2E7;
$main-grey-alt: #c3ddde;
$main-grey-equ: #bdd5dc;
$darkgrey: #688A93;
$main-bggrey: #ECF3F5;
$grey-txt: #A4A4A4;
$main-yellow: #E6E183;
$main-green: #9DD6BE;
$main-red: #EEB4A7;
$main-blue: #8FBCFF;
$main-transparent: rgba(255, 255, 255, 0.95);
$title: #E7504F;

// Header
@import "./fontstyles";

* {
    margin: 0;
    padding: 30;
    box-sizing: border-box;
}

a {
    text-decoration: none;
    font-size: 16px;
    line-height: 24px;
    color: $darkgrey;

    &:hover {
        color: black;
    }
}

body {
    // font-family: 'PT Sans', sans-serif;
    font-family: 'PTSansExpert', sans-serif;
}

.hidden {
    display: none;
}

.wrap {
    display: flex;
    width: 100%;
    position: relative;
}

.container {
    // width: 82%;
    width: 100%;
    padding: 0 25px 0 250px;
}

.flexcontainer {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 1px;
}

// .infocontainer {
//     display: flex;
//     width: 100%;
// }

.innercontainer {
    display: flex;
    width: 100%;
}


.langmenu {
    position: fixed;
    top: 0;
    // width: 18%;
    // min-width: 210px;
    width: 210px;
    display: flex;
    flex-direction: column;
    padding: 40px 30px;
    text-align: left;
    background: $main-bggrey;
    // list-style-type: none;
    overflow-y: scroll;
    height: calc(100vh - 20px);
    // border-radius: 10px;
    margin: 10px;

    &::-webkit-scrollbar {
        width: 12px;
        /* width of the entire scrollbar */
    }

    &::-webkit-scrollbar-track {
        background: white;
        /* color of the tracking area */
    }

    &::-webkit-scrollbar-thumb {
        background-color: $main-bggrey;
        /* color of the scroll thumb */
        border-radius: 20px;
        /* roundness of the scroll thumb */
        border: 3px solid white;
        /* creates padding around scroll thumb */
    }
}

.langlistbutton {
    text-decoration: none;
    white-space: nowrap;
    padding: 3px 5px;
    font-size: 16px;
    text-align: left;
    color: $darkgrey;
    background: none;
    border: none;
    cursor: pointer;

    &:hover {
        color: black;
        background: $main-bggrey;
    }
}

// .langparams {
//     width: 320px;
// }

.langtitle {
    font-size: 64px;
    line-height: 70px;
    padding-top: 25px;
}

.langlist {
    font-size: 18px;
    line-height: 36px;
    width: 314px;
}

.langlisttitle {
    color: $title;
    // border-bottom: 1px solid $main-grey;
    padding-top: 30px;
}


.langinfo {
    font-size: 24px;
    line-height: 31px;
    padding: 40px 0 10px 50px;
    // width: 618px;
    // width: 70%;
}

.legendbg {
    position: sticky;
    width: fit-content;
    top: 10px;
    z-index: 1000;
    padding: 17px;
    // border-radius: 10px;
    background: $main-bggrey;
    display: flex;
    // flex-wrap: wrap;
    align-items: center;
    // margin: 40px 0 -57px 20px;
    margin: 20px 24px 0 0;
    list-style-type: none;
}

.legenbtnswrap {
    display: flex;
    flex-wrap: nowrap;
}

.legenbtns {
    margin: 0 22px 0 0;
    display: flex;
    flex-wrap: nowrap;
}

.legendtextwrap {
    column-count: 3;
    column-gap: 30px;
    margin: 0;
    padding: 0 0 0 20px;
    list-style-type: none;
}

.legendinfowrap {
    width: 320px;
    padding: 10px 20px;
}

.legendinfotext {
    padding: 0 0 10px 0;
}

.altmarkinfo {
    width: 50px;
    padding: 5px 0;
    // margin: 0 20px 0 0;
}

.altmarkinfotext {
    width: 270px;
}

.colorsample {
    width: 15px;
    height: 15px;
}

.legenditem {
    display: flex;
    padding: 3px 0;
}

.legendtext {
    font-size: 12px;
    color: #6F6E6E;
    text-transform: uppercase;
    margin-left: 10px;
    white-space: nowrap;
}



// Togglebuttons

.togglebtns {
    align-self: flex-end;
    font-size: 12px;
    color: #6F6E6E;
    text-transform: uppercase;
    background-color: white;
    border: none;
    padding: 6px 10px;
    margin: 5px 0;
    cursor: pointer;

    &:hover {
        background-color: $darkgrey;
        color: white;
    }
}

.togglebtnsactive {
    background-color: $darkgrey;
    color: white;
    cursor: default;
}



// Letters

.lettersblock {
    position: absolute;
    padding: 0 0 50px 0;
}

.smalltitle {
    font-size: 12px;
    color: #6F6E6E;
    text-transform: uppercase;
    padding: 35px 15px 15px 0;
}

.altborder {
    outline: 2px solid $darkgrey;
    outline-offset: -1px;
    display: flex;
    flex-wrap: nowrap;
}

.altborder_in {
    outline: 3px solid white;
    outline-offset: -3px;
    display: flex;
    flex-wrap: nowrap;
    gap: 1px;
}

.smallunititle {
    position: absolute;
    width: 90px;
    padding: 8px;
}

.letter {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    text-align: center;
    width: 90px;
    height: 90px;
    background: $main-grey;

    &:hover {
        // position: relative;
        opacity: 0.7;
        filter: alpha(opacity=70);
    }
}

.bigletter {
    // font-family: 'Octava', 'PT Sans', sans-serif;
    font-family: 'PTSansExpert', sans-serif;
    font-size: 32px;
    line-height: 32px;
    padding-top: 30px;
}

.bigletterserifitalic {
    font-family: 'PTSerifExpertItalic', sans-serif;
}

.bigletteritalic {
    font-family: 'PTSansExpertItalic', sans-serif;
}

.bigletterserif {
    font-family: 'PTSerifExpert', sans-serif;
}

.notrussiansign {
    background: $main-grey;
}

.dialectsign {
    background: $main-green;
}

.oldersign {
    background: $main-red;
}

.lexicosign {
    background: $main-blue;
}

.altsign {
    background: $main-grey-alt;
}

.equsign {
    background: $main-grey-equ;
}

.featuresign {
    font-feature-settings: "aalt";
    -moz-font-feature-settings: "aalt";
    -webkit-font-feature-settings: "aalt";
}

.markplace {
    text-align: right;
    // padding: 0 5px 10px 10px;
    margin: 0 6px 10px 5px;
}

.altmark {
    width: 26px;
    text-align: center;
    font-size: 10px;
    padding: 1px 6px 3px 6px;
    border-radius: 10px;
    color: $grey-txt;
    background: $main-transparent;
}


// Media

@media (max-width:1255px) {

    .langlist {
        width: 224px;
    }

    .legendtextwrap {
        column-count: 1;
        column-gap: 0;
        padding: 0 20px;
    }

}

@media (max-width:996px) {

    .innercontainer {
        flex-direction: column;
    }

    .langlist {
        width: 100%;
    }

    .langinfo {
        padding: 40px 0 10px 0;
    }

    .legenbtnswrap {
        flex-direction: column;
        width: 142px;
    }

    .legendbg {
        align-items: flex-start;
    }

}

